<template>
  <div>
    <h1>我是列表详情</h1>
    <button @click="goList()">去列表页面</button>
    <h1>当前动态参数为{{$route.params.id}}</h1>
  </div>
</template>
<script>
export default {
  methods: {
    goList() {
      this.$router.push("/list");
    }
  },
  //组件守卫更新
  beforeRouteUpdate(to, from, next) {
      //组件不会被重新渲染 只更新动态参数 触发更新守卫
    console.log(to, "UUUUttt");
    console.log(from, "UUUUUffff");
    console.log(this, "UUUUUUthis");
    //next()
  }
};
</script>
<style lang="">
</style>


