<template>
  <div id="home">
    <div class="nav">
      <router-link active-class="red" :to="item.url" v-for="(item,i) in nav" :key="i">
        <div @click="abc(i)" class="items">
          <img :src="$route.path == item.url? item.icon2:item.icon" alt />
          {{item.title}}
        </div>
      </router-link>
    </div>

    <router-view></router-view>
    <div class="footer">
      啊哦,已经到底了.
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      nav: [
        {
          title: "首页",
          url: "/index",
          icon: require("../assets/mobile/余益惠首页/首页 icon/标签栏/首页 未触发.png"),
          icon2: require("../assets/mobile/余益惠首页/首页 icon/标签栏/首页 触发.png")
        },
        {
          title: "医检",
          url: "/doctor",
          icon: require("../assets/mobile/余益惠首页/首页 icon/标签栏/医检 未触发.png"),
          icon2: require("../assets/mobile/余益惠首页/首页 icon/标签栏/医检 触发.png")
        },
        {
          title: "资讯",
          url: "/list",
          icon: require("../assets/mobile/余益惠首页/首页 icon/标签栏/资讯 未触发.png"),
          icon2: require("../assets/mobile/余益惠首页/首页 icon/标签栏/资讯 触发.png")
        },
        {
          title: "论坛",
          url: "/know",
          icon: require("../assets/mobile/余益惠首页/首页 icon/标签栏/论坛 未触发.png"),
          icon2: require("../assets/mobile/余益惠首页/首页 icon/标签栏/论坛 触发.png")
        },
        {
          title: "我的",
          url: "/mine",
          icon: require("../assets/mobile/余益惠首页/首页 icon/标签栏/我的 未触发.png"),
          icon2: require("../assets/mobile/余益惠首页/首页 icon/标签栏/我的 触发.png")
        }
      ],
      state: 0
    };
  },
  methods: {
    abc(i) {
      console.log(this.$route.path,"111111111");
      console.log(this.nav[i].url);
      
      
    }
  }
};
</script>
<style  scoped lang="less">
@main: #1f7d9b;
@dark: #4c4c4c;
@tint: #999;
#home {
  width: 320px;
  color: @dark;
  background-color: #f7f7f7;
  .red {
    color: @main;
  }
  .nav {
    position: fixed;
    width: 320px;
    bottom: 0;
    padding: 4px 0;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    background-color: #fff;
    .items {
      height: 37px;
      width: 26px;
      text-align: center;
      img {
        width: 26px;
        height: 26px;
        display: block;
      }
    }
  }
  .footer{
    width: 320px;
    height: 48px;
    line-height: 48px;
    text-align: center;
  }
}
</style>
 


