//第一步 引入核心库
import Vue from 'vue'
//第二步 引入vuex
import Vuex from 'vuex'

Vue.use(Vuex)
const moduleA = {
    state:{
        str:'我是模块A的状态'
    },
    mutations:{},
    actions:{},
    getters:{},
    modules:{}
}

const Store = new Vuex.Store({
    state:{
        a:'vuex中的a',
        msg:'vuex中的msg',
        num:10
    },
    mutations:{
        // 第一个参数就是state,第二个参数是actions传过来的
        addNum(state,x){
            console.log(state,x,'mutations')
          //  state.num += 2
          state.num += x
        }
    },
    actions:{
        aa({commit}){
            commit('addNum',6)
        }
    },
    getters:{
        //相当于computer计算属性
        getInfo(state){
            console.log('我进入getter')
           // return 15
           state.num +=1
           return state.num %2 == 0? '我被2整除':"我不能被2整除"
        }
    },
    modules:{
        moduleA
    }

})

export default Store


