<template>
  <div>
    <h1>我是首页</h1>
    <h1>vuex,a : ---- {{$store.state.a}}</h1>
    <hr />
    <h2>{{msg}}</h2>
    <hr />
    <h3>{{info}}</h3>
    <hr />
    <h1>{{$store.state.num}}</h1>
    <button @click="changNum ()">点击触发vuex中的方法</button>
    <hr />
    <h2>vuex中getters的数据{{$store.getters.getInfo}}</h2>
    <h1>vuex中模块a:{{$store.state.moduleA.str}}</h1>
  </div>
</template>
<script>
export default {
  data() {
    return {
      msg: this.$store.state.msg
    };
  },
  mounted() {
    // console.log(this, "home组件");
    // console.log(this.$store.state.a)
  },
  // 计算属性
  computed: {
    info() {
      return this.$store.state.msg;
    }
  },
  methods: {
    changNum() {
      // console.log("我准备dispatch");
      //自定义一个事件名
      //直接通过 commit 去操作mutation
      // this.$store.commit('addNum',5)
      //通过 dispatch 去触发action行动 从而提交更改信息给mutation 触发state状态的变化
      this.$store.dispatch("aa");
    }
  }
};
</script>
<style lang="">
</style>


