import Vue from 'vue'
import App from './App'
import router from './router'
//引入vuex 的仓库  
import store from './store'
// import './assets/css/iconfont.css'
/* 
创建全局过滤器   最新版在https://github.com/caoxueyang
Vue.filter('b',filter) */
import filter from './filter'
// console.log(filter,'filtera ')
for(let i in filter){
  Vue.filter(i,filter[i])
}

//引入全局组件  最新版在https://github.com/caoxueyang
/* import vBack from './components/common/back'
Vue.component("vBack",vBack) */
/* import nav from './nav'
Vue.component('组件名称','内容') */
import cxyCommon from './components/common'
console.log(cxyCommon,'cxyCommon ')
for(let i in cxyCommon){
  Vue.component(i,cxyCommon[i])
}

// 自适应
import 'babel-polyfill'
import 'lib-flexible'
import './assets/reset.css'
// 这句啥意思
Vue.config.productionTip = false
// eleUI
import ElementUI from "element-ui"
import "element-ui/lib/theme-chalk/index.css"
Vue.use(ElementUI)
//animate
import animated from 'animate.css'
Vue.use(animated)

router.afterEach((to, from, next) => {
  window.scrollTo(0, 0);
})
// axios
import axios from 'axios'
Vue.prototype.$axios = axios

// 设置axios向后端请求的地址

router.beforeEach((to, from, next) => {
  console.log(process.env.NODE_ENV);
  if (process.env.NODE_ENV == "development") {
    axios.defaults.baseURL = "/api"
  } else if (process.env.NODE_ENV == "production") {
    axios.defaults.baseURL = "/api" //上线生产环境也需要加api
  } else {
    axios.defaults.baseURL = ""
  }
  //from.path 是不可控的 
  //to.path 控制你要去的地点 去进行逻辑拦截
  // if (sessionStorage.getItem('flag') || to.path == '/login') {
  //   console.log('已登录')
  //   next()
  // } else {
  //   console.log('未登录')
  //   next('/login')
  // }
  next();
})
/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  components: {
    App
  },
  template: '<App/>'
})



