import Vue from 'vue'
import Router from 'vue-router'
//一级路由
import Home from '@/components/home'
import listDetail from '@/components/listDetail'
import Login from '@/components/login'

//二级路由
// home的开始
import Index from '@/components/index'
const Doctor = () => import('../components/doctor')
const List = () => Promise.resolve(import('../components/list'))
const Know = () => import('../components/know')
const Mine = () => import('../components/mine')

// home的结束



Vue.use(Router)

const router = new Router({
  routes: [{
      path: '/home',
      component: Home,
      redirect:'/index',
      children: [
        {
          path: '/index',
          component: Index
        },
        {
          path: '/doctor',
          component: Doctor
        },
        {
          path: '/list',
          component: List
        },
         {
          path: '/know',
          component: Know
        },

        {
          path: '/mine',
          component: Mine
        }
      ]
    },
    {
      path: '/listDetail/:id',
      name: 'listDetail',
      component: listDetail
    },
    {
      path: '/login',
      component: Login,
      //路由内部导航钩子函数
      beforeEnter: (to, from, next) => {
        // console.log(to, '路由内导航-去哪')
        // console.log(from, '路由内导航-从哪来')
        next()
      }
    },
    {
      path: '*',
      redirect: '/home'
    }
  ]
})
export default router
// 全局导航守卫 , 浏览端判断
function browserRedirect() {
  var sUserAgent = navigator.userAgent.toLowerCase();
  var bIsIpad = sUserAgent.match(/ipad/i) == "ipad";
  var bIsIphoneOs = sUserAgent.match(/iphone os/i) == "iphone os";
  var bIsMidp = sUserAgent.match(/midp/i) == "midp";
  var bIsUc7 = sUserAgent.match(/rv:1.2.3.4/i) == "rv:1.2.3.4";
  var bIsUc = sUserAgent.match(/ucweb/i) == "ucweb";
  var bIsAndroid = sUserAgent.match(/android/i) == "android";
  var bIsCE = sUserAgent.match(/windows ce/i) == "windows ce";
  var bIsWM = sUserAgent.match(/windows mobile/i) == "windows mobile";
  if (
    bIsIpad ||
    bIsIphoneOs ||
    bIsMidp ||
    bIsUc7 ||
    bIsUc ||
    bIsAndroid ||
    bIsCE ||
    bIsWM
  ) {
    console.log("手机登录");
  } else {
    console.log("电脑登录,为您跳转去电脑端吧");
  }
}
router.beforeEach((to, from, next) => {
  // console.log("全局守卫");
  browserRedirect();
  next()
});



